import React from 'react';
import { Container, Row, Col, Card, ProgressBar } from 'react-bootstrap';

const About = () => {
  const softSkills = [
    {
      title: "Passionn",
      icon: "fas fa-heart",
      description: "Je suis une personne passionne par le domaine du dveloppement et des nouvelles technologies. Cette passion me pousse  rester motiv, curieux et engag dans les projets que j'entreprends, en cherchant toujours  amliorer mes comptences et  atteindre des rsultats de qualit.",
      level: 95
    },
    {
      title: "Adaptation",
      icon: "fas fa-people-arrows",
      description: "Je m'adapte facilement aux diffrents environnements de travail. Que ce soit dans une quipe dynamique, un cadre formel ou une structure en volution, je sais ajuster ma manire de travailler afin de contribuer efficacement et de maintenir une bonne collaboration avec mes collgues.",
      level: 90
    },
    {
      title: "Travail sous pression",
      icon: "fas fa-tachometer-alt",
      description: "Je suis capable de travailler efficacement mme dans des situations de pression ou de dlais serrs. Plutt que de cder au stress, j'utilise ces moments comme une opportunit pour dmontrer mon sens de l'organisation, ma concentration et ma capacit  trouver rapidement des solutions adaptes.",
      level: 85
    },
    {
      title: "Apprentissage rapide",
      icon: "fas fa-bolt",
      description: "J'ai une forte capacit d'apprentissage, ce qui me permet de matriser rapidement de nouveaux outils ou technologies. Grce  ma curiosit et  ma mthodologie, je peux assimiler les concepts essentiels et les mettre en pratique en peu de temps, ce qui me rend oprationnel rapidement.",
      level: 92
    }
  ];

  return (
    <section id="about" className="py-5 bg-light">
      <Container>
        <Row className="mb-5">
          <Col>
            <h2 className="text-center fw-bold mb-4"> propos de moi</h2>
            <p className="text-center text-muted lead mx-auto" style={{maxWidth: '1200px'}}>
              Je suis tudiant en Technologies de lInformation  lInstitut Suprieur des 
              tudes Technologiques de Klibia (ISET Klibia). Passionn par le dveloppement 
              web et les solutions numriques, je mefforce de crer des projets modernes, efficaces
               et centrs sur lutilisateur. Dot dune grande capacit dadaptation et dapprentissage, 
               je suis toujours prt  relever de nouveaux dfis pour amliorer mes comptences et apporter 
               des solutions innovantes.
            </p>
          </Col>
        </Row>

        {/* Section Comptences personnelles */}
        <Row>
          <Col>
            <h3 className="text-center mb-5">Mes Forces Personnelles</h3>
          </Col>
        </Row>

        <Row>
          {softSkills.map((skill, index) => (
            <Col key={index} lg={6} className="mb-4">
              <Card className="h-100 border-0 shadow-sm hover-card">
                <Card.Body className="p-4">
                  <div className="d-flex align-items-center mb-3">
                    <div className="skill-icon me-3">
                      <i className={`${skill.icon} text-primary fa-2x`}></i>
                    </div>
                    <div>
                      <Card.Title className="h5 mb-1">{skill.title}</Card.Title>
                      <div className="d-flex justify-content-between align-items-center">
                        <small className="text-muted">Niveau de matrise</small>
                        <span className="badge bg-primary">{skill.level}%</span>
                      </div>
                    </div>
                  </div>
                  
                  <ProgressBar 
                    now={skill.level} 
                    className="mb-3"
                    variant={getProgressVariant(skill.level)}
                  />
                  
                  <Card.Text className="text-muted">
                    {skill.description}
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>

        {/* Section Exprience et ducation */}
        <Row className="mt-5">
          <Col lg={6} className="mb-4">
            <Card className="h-100 border-0 shadow-sm">
              <Card.Header className="bg-primary text-white">
                <h5 className="mb-0">
                  <i className="fas fa-graduation-cap me-2"></i>
                  Parcours Acadmique
                </h5>
              </Card.Header>
              <Card.Body>
                <div className="timeline">
                  <div className="timeline-item mb-4">
                    <div className="d-flex justify-content-between align-items-start mb-2">
                      <h6 className="mb-0">Licence en Technologies de lInformation</h6>
                      <span className="badge bg-secondary">2024 - Prsent</span>
                    </div>
                    <p className="text-muted small mb-0">
                      Formation axe sur le dveloppement logiciel, les systmes dinformation, 
                      la gestion de bases de donnes et les technologies web modernes. 
                      Dveloppement des comptences en programmation, conception et scurit informatique.
                    </p>
                  </div>
                  <div className="timeline-item">
                    <div className="d-flex justify-content-between align-items-start mb-2">
                      <h6 className="mb-0">Baccalaurat  Spcialit Technique</h6>
                      <span className="badge bg-secondary">2023</span>
                    </div>
                    <p className="text-muted small mb-0">
                      Diplme obtenu avec une moyenne de <strong>14.52</strong>. 
                      Spcialisation oriente vers les disciplines techniques et informatiques, 
                      combinant thorie scientifique et applications pratiques.
                    </p>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>

          <Col lg={6} className="mb-4">
            <Card className="h-100 border-0 shadow-sm">
              <Card.Header className="bg-success text-white">
                <h5 className="mb-0">
                  <i className="fas fa-briefcase me-2"></i>
                  Expriences Professionnelles
                </h5>
              </Card.Header>
              <Card.Body>
                <div className="timeline">
                  <div className="timeline-item mb-4">
                    <div className="d-flex justify-content-between align-items-start mb-2">
                      <h6 className="mb-0">Stage  Socit Atomic IT Pro</h6>
                      <span className="badge bg-secondary">16 juin 2025 - 12 juillet 2025</span>
                    </div>
                    <p className="text-muted small mb-0">
                      Participation au dveloppement et  la maintenance dapplications web
                      Contribution aux projets en quipe et amlioration des comptences en dveloppement full stack.
                      <br />Dveloppement dune application web de location et vente de maisons, <strong>intgrant un modle de machine learning.</strong>
                    </p>
                  </div>
                  <div className="timeline-item">
                    <div className="d-flex justify-content-between align-items-start mb-2">
                      <h6 className="mb-0">Stage  Socit Yes Technologie</h6>
                      <span className="badge bg-secondary">10 juin 2024 - 3 fvrier 2024</span>
                    </div>
                    <p className="text-muted small mb-0">
                      Dcouverte du milieu professionnel et initiation au dveloppement web. 
                      Participation  des tches de conception et dintgration dinterfaces utilisateur.
                      <br />Dveloppement dun site vitrine (statique)
                    </p>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>

        </Row>
      </Container>
    </section>
  );
};

// Fonction utilitaire pour les variantes de ProgressBar
const getProgressVariant = (level) => {
  if (level >= 90) return 'success';
  if (level >= 80) return 'info';
  if (level >= 70) return 'warning';
  return 'secondary';
};

export default About;